import React, { useState } from 'react';
import Message from './comments/compontents/message/submessage/Message';




export default function MessageScroll(props) {

    const { post } = props;
    const [message, setMessage] = useState([]);

    



    return (
        <>
            {post.comments.map((comment, index) => {
                return <div><Message editable={false} comment={comment} /></div>;
            })}


            <div className="buttonBar">
                <div className="loader"></div>
            </div>
        </>
    );
}

